import './App.css';
import { useState, useEffect } from 'react'
import axios from 'axios';
import Pokemon from './pokemon';
import { FaSearch, FaArrowAltCircleDown } from 'react-icons/fa'

function App() {
  const [pokemons, setPokemons] = useState([])
  const [pokeInput, setPokeInput] = useState('')
  const [count, setCount] = useState(9)
  const cancelToken = axios.CancelToken
  const source = cancelToken.source()

  useEffect(() => {
    for (let i = 1; i < 899; i++) {
      axios.get(`https://pokeapi.co/api/v2/pokemon/${i}`, {
        cancelToken: source.token
      })
        .then(res => {
          setPokemons((poke) => {
            return [...poke, res.data]
          })
        })
        .catch(err => {
          console.log(err)
        })
    }
    return () => source.cancel()
  }, [])

  const pokeSort = pokemons.filter((pokemon) => {
    return pokemon.name.toLowerCase().includes(pokeInput.toLowerCase())
  })

  const pokeInputChange = (e) => {
    setPokeInput(e.target.value)
    e.preventDefault()
  }

  const countPlus = () => {
    setCount(count + 10)
  }

  const doNothing = (e) => {
    e.preventDefault()
  }

  return (
    <div className="App">
      <form onSubmit={doNothing} className='search'>
        <h1>Search Pokemon</h1>
        <div className='form'>
          <input
            onChange={pokeInputChange}
            type="text"
            placeholder='Search Pokemon'
            id="pokeSearchBar"
            className='searchbar'
          />
          <span className='search_icon'><FaSearch /></span>
          <div className="extra1"></div>
          <div className="extra2"></div>
          <button onClick={doNothing} className='go'>GO</button>
        </div>
      </form>

      <div className='pokemons_container'>
        <div className={`pokemon pokemon-dark`}>
          <p className="img-head">Pokemon</p>
          <p className="name">Name</p>
          <p className="weight">Weight</p>
          <p className="height">Height</p>
          <p className="type">Type</p>
          <p className="moves">Moves</p>
          <p className="abilities">Abilities</p>
        </div>
        {
          pokeSort.map((pokemon, index) => {
            return (
              index < count && <Pokemon
                key={index}
                name={pokemon.name}
                type={pokemon.types}
                moves={pokemon.moves}
                height={pokemon.height}
                weight={pokemon.weight}
                front_image={pokemon.sprites.front_default}
                back_image={pokemon.sprites.back_default}
                abilities={pokemon.abilities}
                condition={index % 2 === 0 ? false : true}
              />
            )
          })
        }
      </div>
      <div className="next"><button onClick={countPlus}><span>More Pokemons </span><FaArrowAltCircleDown /></button></div>
      <div className="extra"></div>
    </div>
  );
}

export default App;
