import { React } from 'react';
import './pokemon.css';

const pokemon = ({ condition, name, type, moves, height, weight, front_image, back_image, abilities }) => {
    return (
        <div className={`pokemon ${condition ? 'pokemon-dark' : 'pokemon-light'}`}>
            <p className={`img ${condition ? 'img-dark' : 'img-light'}`}><img src={front_image} alt="" /></p>
            <p className="name">{name}</p>
            <p className="weight">{weight}</p>
            <p className="height">{height}</p>
            <p className="type">
                {
                    type.map((types, index) => {
                        return <span key={index}>{types.type.name}</span>
                    })
                }
            </p>
            <p className="moves">
                {
                    moves.map((move, index) => {
                        return index < 3 && <span key={index}>{move.move.name}</span>
                    })
                }
            </p>
            <p className="abilities">
                {
                    abilities.map((ability, index) => {
                        return index < 3 && <span key={index}>{ability.ability.name}</span>
                    })
                }
            </p>
        </div>
    )
}

export default pokemon;